import pool from "@/app/database";
import { NextResponse } from "next/server";

export async function GET(_request: Request, { params }: { params: { nombre: string } }) {
  const nombre = params.nombre; // Aquí debería llegar "Luis Pedro" ya decodificado

  // Verificar que nombre no sea vacío
  if (!nombre) {
    return NextResponse.json({ error: 'Nombre inválido' }, { status: 400 });
  }

  try {
    const [rows]: any = await pool.query('SELECT * FROM usuarios WHERE nombre = ?', [nombre]);

    if (rows.length === 0) {
      return NextResponse.json({ error: 'Usuario no encontrado' }, { status: 404 });
    }

    return NextResponse.json(rows[0]);
  } catch (error: any) {
    console.error(error);
    return NextResponse.json({ error: 'Error en la consulta', details: error.message }, { status: 500 });
  }
}
